.about__container {
	display: grid;
	grid-template-columns: 35% 50%;
	gap: 15%
}

.about__me {
	width: 100%;
	aspect-ratio: 1/1;
	background: linear-gradient(
		45deg,
		transparent,
		var(--color-primary),
		transparent
	); 
	display: grid;
	place-items: center;
	border-radius: 2rem;
}

.about__me-image {
	transform: rotate(15deg);
	border-radius: 2rem;
	overflow: hidden;
	transition: var(--transition)
}

.about__me-image:hover {
	transform: rotate(0);
}

.about__cards {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	gap: 1.5rem;
	
}

.about__card {
	background: var(--color-bg-variant);
	border: 1px solid transparent;
	border-radius: 2rem;
	text-align: center;
	padding: 2rem;
	transition: var(--transition);
	/* when hovering cursor doesnt become text cursor */
	cursor: default;
}

.about__card:hover {
	background: transparent;
	border-color: var(--color-primary-variant);
}

.about__icon {
	color: var(--color-primary);
	font-size: 1.4rem;
	margin-bottom: 1rem;
}

.about__card small {
	font-size: 0.7rem;
	color: var(--color-light)
}

.about__content p {
	margin: 2rem 0 2.6rem;
}


@media screen and (max-width: 1024px) {
	.about__container {
		grid-template-columns: 1fr;
		gap: 0;
	}

	.about__me {
		width: 50%;
		margin: 2rem auto 4rem;
	}

	/* tbd margins look kinda weird rn will have to change */
	/* .about__content p {
		margin: 1rem 0 1.5rem
	} */
}

@media screen and (max-width: 600px) {
	.about__me {
		width: 65%;
		margin: 0 auto 3rem;
	}

	.about__cards {
		grid-template-columns: 1fr 1fr;
		gap: 1rem;
	}

	.about__content {
		text-align: center;
	}

	.about__content p {
		margin: 1.5rem 0;
	}
}